(function() {
  'use strict';

  angular
    .module('chatbox')
    .directive('sideBar', sideBar);
  /** @ngInject */
  function sideBar($timeout) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'main',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      vm.messageList = [];
      vm.showDirection = false;
      vm.setShowDirection = setShowDirection;
      vm.directionClick = directionClick;
      vm.showDirectionWork = false;
      vm.directionWorkClick = directionWorkClick;
      vm.maximunMessage = 3;
      vm.listState = [{
        title: "Advertising",
        key: "adv"
      },{
        title: "Branding & Design",
        key: "brand",
        direction: [{
          title: "The Center for Children and Families",
          key: "brand-center"
        },{
          title: "1st Okalahoma homes",
          key: "brand-okalahoma"
        }]
      },{
        title: "Digitar Marketing",
        key: "marketing"
      },{
        title: "Graphic Design",
        key: "marketing"
      }];
      //List item for direction tell me more
      vm.directionTellMeMoreItems = [];
      //Clone list item direction
      angular.copy(vm.listState,vm.directionTellMeMoreItems);
      addMessage("Hello, this is Tom.", false, 1000, function(){
        addMessage("Welcome to Freestyle", false, 1200,function() {
          addMessage("Should I tell more about us or do you want to...", false, 1500, function(){
            $timeout(vm.setShowDirection,500);
          })
        })
      });
  
      /**
       * Handle add message
       * @param {*} message 
       * @param {*} isThey the flag to identify which message is of customer (true for customer, false for system)
       * @param {*} timeout timeout of message to be waiting to show
       * @param {*} callback function call after message showed
       */
      function addMessage(message, isThey, timeout, callback) {
        var timeout = timeout || 0;
  
        var item = {
          message: "...",
          isThey: isThey
        };
        //Add empty message
        vm.messageList.push(item);
        $timeout(function() {
          item.message = message;
          //Remove first chat item if number of messages greater than maximun config
          if (vm.messageList.length > vm.maximunMessage) {
            vm.messageList.shift();
          }
          //Call callback function if any
          if (callback) {
            callback();
          }
        },timeout);
      }
      
      /**
       * Show direction element
       */
      function setShowDirection() {
        vm.showDirection = true;
      }
  
      /**
       * Redirect target frame to url
       * @param {*} url 
       */
      function goTo(url) {
        var frame = $("#targetFrame");
        if (frame.length > 0) {
          frame.attr('src', url);
        }
      }
  
      /**
       * Return to main direction
       */
      function returnToMainDirection() {
        $timeout(vm.setShowDirection,500);
        // addMessage("Should I tell more about us or do you want to be", false, 1000, function(){$timeout(vm.setShowDirection,500);});
      }
  
      /**
       * Handle click direction to redirect new state
       * @param {any} type type of direction
       */
      function directionClick(type) {
        switch(type) {
          case 'tell':
            //Add message
            vm.messageList.push({
              message: 'Tell me more',
              isThey: true
            });
            //Remove first chat item
            vm.messageList.shift();
            //Hide direction
            vm.showDirection = false;
            //Add next message
            addMessage("Which category would you like to check out?", false, 1000, function(){
              vm.showDirectionWork = true;
            });
            break;
          case 'contact': 
            //Add message
            addMessage("Contact me", true, 0);
            //Hide direction
            vm.showDirection = false;
            // Redirect iframe to work zone
            goTo("http://www.freestyleokc.com/contact/#post-3974");
            //Show message directed contact
            addMessage("Please send us a request.", false, 1500,function(){$timeout(returnToMainDirection,500)});
            break;
        }
      }
  
      /**
       * Redirect work
       * @param {*} type 
       */
      function directionWorkClick(type) {
        if (type && type.key) {
          switch (type.key) {
            case 'brand':
              //Add message
              addMessage(type.title, true);
              //Update direction by lookup in list state then update
              if (type.direction) {
                vm.directionTellMeMoreItems = type.direction;
              }
              break;
            case 'brand-okalahoma':
              //Add message
              addMessage(type.title, true);

              //Hide direction work
              vm.showDirectionWork = false;
    
              //Show message directed photo
              addMessage("Welcome to our 1st Oklahoma Home pages!", false, 1500,function() {$timeout(returnToMainDirection,500)});
              break;
          }

          if (!type.direction) {
            //If not direction here, reset directionTellMeMoreItems;
            angular.copy(vm.listState,vm.directionTellMeMoreItems);
          }
        } else {
          console.log("Not found type of your selected item");
        }
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('chatbox')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('chatbox')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
    'use strict';
  
    angular
    .module('chatbox')
    .directive('moveOver', moveOver);
  /** @ngInject */
  function moveOver($timeout) {
    function link(scope, element, attrs) {
        if (element && element.mouseover) {
          //Handle move over
          element.mouseover(function(){
             //Find item back ground fade and change opacity value
             itemHover(this,true);
          });
          //Handle move out
          element.mouseout(function(){
            //Find item back ground fade and change opacity value
            itemHover(this,false);
         });
        }

        function itemHover(me,isHover) {
          var opacityOverlayValue = (isHover) ? 0.85 : 0,
              opacityText = (isHover) ? 1 : 0,
              transformText = (isHover) ? "scale(1)" : "scale(0.9)",
              transformImage = (isHover) ? "scale(1.05, 1.05)" : "scale(1)",
              overlay = me.querySelector(".item-overlay"),
              image = me.querySelector("img"),
              text = me.querySelector(".text");
          //Find item back ground fade and change opacity value
          if (overlay) {
            $(overlay).css("opacity",opacityOverlayValue);
          }
          //Find and update text
          if (text) {
            $(text).css({"opacity" : opacityText ,transform: transformText});
          }
          //Find and update image
          if (image) {
            $(image).css({transform: transformImage});
          }
        }
    }
    return {
      link: link
    };
  }
  
  })();
  
